import request from '@/utils/request'

// 查询项目单列表
export function listProject(query) {
  return request({
    url: '/business/project/list',
    method: 'get',
    params: query
  })
}
// 查询项目单详细-taskId
export function getProjectByTaskId(taskId) {
  return request({
    url: '/business/project/taskId/' + taskId,
    method: 'get'
  })
}

// 查询项目单详细
export function getProject(id) {
  return request({
    url: '/business/project/' + id,
    method: 'get'
  })
}

// 新增项目单
export function addProject(data) {
  return request({
    url: '/business/project',
    method: 'post',
    data: data
  })
}

// 修改项目单
export function updateProject(data) {
  return request({
    url: '/business/project',
    method: 'put',
    data: data
  })
}

// 删除项目单
export function delProject(id) {
  return request({
    url: '/business/project/' + id,
    method: 'delete'
  })
}
