import request from '@/utils/request'

// 查询待办任务列表
export function getTaskFinishByPage(query) {
  return request({
    url: '/business/projectTask/getTaskFinishByPage',
    method: 'get',
    params: query
  })
}
// 查询待办任务列表
export function getTaskWaitByPage(query) {
  return request({
    url: '/business/projectTask/getTaskWaitByPage',
    method: 'get',
    params: query
  })
}
// 查询任务单列表
export function listProjectTask(query) {
  return request({
    url: '/business/projectTask/list',
    method: 'get',
    params: query
  })
}

// 查询任务单详细
export function getProjectTask(id) {
  return request({
    url: '/business/projectTask/' + id,
    method: 'get'
  })
}

// 新增任务单
export function addProjectTask(data) {
  return request({
    url: '/business/projectTask',
    method: 'post',
    data: data
  })
}
// 更新 环节办理人
export function updateTaskAssginee(data,taskId){
  return request({
    url: '/business/projectTask/updateTaskAssginee/'+taskId,
    method: 'post',
    data: data
  })
}
// 修改任务单-事业部
export function updateByAllocate(data) {
  return request({
    url: '/business/projectTask/updateByAllocate/'+data.id,
    method: 'put',
    data: data
  })
}
// 修改任务单
export function updateProjectTask(data) {
  return request({
    url: '/business/projectTask',
    method: 'put',
    data: data
  })
}

// 删除任务单
export function delProjectTask(id) {
  return request({
    url: '/business/projectTask/' + id,
    method: 'delete'
  })
}
