import request from '@/utils/request'

// 获取列表-计量
export function getWorkLogList(query) {
  return request({
    url: '/archive/taskWorklog/list',
    method: 'get',
	params:query
  })
}
// 获取信息-计量
export function getWorkLogById(id) {
  return request({
    url: '/archive/taskWorklog/'+id,
    method: 'get'
  })
}
// 新增-计量
export function workLogAdd(data) {
  return request({
    url: '/archive/taskWorklog',
    method: 'post',
    data: data
  })
}
// 修改-计量
export function workLogModify(data) {
  return request({
    url: '/archive/taskWorklog',
    method: 'put',
    data: data
  })
}

