import request from '@/utils/request'

// 查询档案项目列表
export function listProject(query) {
  return request({
    url: '/archive/project/list',
    method: 'get',
    params: query
  })
}

// 查询档案项目详细
export function getProject(id) {
  return request({
    url: '/archive/project/' + id,
    method: 'get'
  })
}

// 新增档案项目
export function addProject(data) {
  return request({
    url: '/archive/project',
    method: 'post',
    data: data
  })
}

// 修改档案项目
export function updateProject(data) {
  return request({
    url: '/archive/project',
    method: 'put',
    data: data
  })
}

// 删除档案项目
export function delProject(id) {
  return request({
    url: '/archive/project/' + id,
    method: 'delete'
  })
}
